import { Component, OnInit } from '@angular/core';
import { faArrowLeft, faSignInAlt } from '@fortawesome/free-solid-svg-icons';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { ApiService } from './../services/api.service';
import { DataService } from './../services/data.service';
import {
  FormBuilder,
  FormGroup,
  FormControl,
  Validators,
} from '@angular/forms';
import { HttpHeaders } from '@angular/common/http';
import { AmazonLoginProvider } from 'angularx-social-login';

@Component({
  selector: 'top-up-app-payment',
  templateUrl: './top-up-payment.component.html',
  styleUrls: ['./top-up-payment.component.css'],
})
export class TopUpPaymentComponent implements OnInit {
  _selectedAmount: any;
  _typedAmount: any;
  amountTosend: any;
  myVar2 = false;
  userName: any;
  userVerified: any;
  faArrowLeft = faArrowLeft;
  faSignInAlt = faSignInAlt;
  token: any;
  balance: any;
  termErrorMessage: boolean = false;
  errorMessage: any;
  lessNumber: boolean = false;
  notNumber: boolean = false;
  approvedNumber: boolean = false;
  touchedinput: boolean = false;

  payNowForm = this.formBuilder.group({
    cardNumber: '',
    cardName: '',
    passworcardNamed: '',
    expiry: '',
    billingAddress: '',
    city: '',
    defaultCard: '',
    amountToRecharge: new FormControl('', [Validators.pattern("^[0-9]*$"),Validators.min(501)]),
    saveCard: 'false',
  });

  constructor(
    private http: HttpClient,
    private router: Router,
    private apiService: ApiService,
    private dataservice : DataService,
    private formBuilder: FormBuilder
  ) { }

  ngOnInit(): void {

     this.token = sessionStorage.getItem('token');

    this.apiService.getUserBalance(this.token).subscribe(
      (response) => {
       this.balance = response.body.availableBalance;
        },
      (error) => {
        console.log(error);
      });

    this.dataservice.user.subscribe((user: string) => {
      this.userName = user;
      console.log(this.userName);
    });
  }

  get amountToRecharge(){
  return this.payNowForm.get('amountToRecharge')
  }

  valuechange(newValue: any) {
    console.log(this._selectedAmount)
    console.log(this._typedAmount)

    this._selectedAmount = ''
    this._typedAmount = newValue;
    if (newValue >= 501) {
      this.approvedNumber = true
    }
  }

  selectedAmount(amount: any) {
    console.log(amount)
    console.log(this._typedAmount)
    this._selectedAmount = amount;
  }
  onSubmit(): void {
    if (this.payNowForm.valid) {
    if (this.approvedNumber == true) {
      this.amountTosend = this._typedAmount;
      //    .setValue(this._typeedAmount)
    }else if (this._selectedAmount !== '') {
      this.amountTosend = this._selectedAmount;
    }

    if (this.myVar2 == true) {

      this.dataservice.token.subscribe((token: any) => {
        this.token = token;
      });

const pay = {amountToRecharge: this.amountTosend,
billingAddress: this.payNowForm.value.billingAddress,
cardName: this.payNowForm.value.cardName,
cardNumber: this.payNowForm.value.cardNumber,
city: this.payNowForm.value.city,
defaultCard: this.payNowForm.value.defaultCard,
expiry: this.payNowForm.value.expiry,
passworcardNamed: this.payNowForm.value.passworcardNamed,
saveCard: "false"
}
console.log(pay);

      const headers = new HttpHeaders()
        .set('Authorization', `Bearer ${this.token}`)
        .set('content-type', 'application/json')
        .set('Access-Control-Allow-Origin', '*');
      this.http
        .post(
          'https://luck-buy.herokuapp.com/api/payment',
          pay,
          { headers: headers }
        )
        .subscribe(
          (res) => {
            this.payNowForm.reset();
           // console.log(res);
           this.deducttion()
          },
          (error) => {
            this.errorMessage = error.error.message;
          }
        );
    } else {
      this.termErrorMessage = true;
       }
  }
}
deducttion(){
  this.router.navigate(['/top_up_payment_messege/1']);
      
}
}