import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { baseUrl } from '../helper/base_url';
import { HttpHeaders } from '@angular/common/http';
import { DataService } from '../services/data.service';
import {ChangePassword} from '../model/ChangePassword';
import {userMe} from './../helper/user_me.interface';
import {User} from "../helper/user.interface";
import {UserProfile} from "../model/UserProfile";

@Injectable({
  providedIn: 'root',
})
export class ApiService {
  myHeaders: any;
  token: any;
  requestOptions: any;
  constructor(
    private dataservice: DataService,
    private httpClient: HttpClient
  ) { }


  /* when user  getting the first look */
  getDraw(filter: any) {
    return this.httpClient.get(baseUrl + `draw/public?status=${filter}`);
  }

  drawDetails(filter: any) {
    return this.httpClient.get(baseUrl + `draw/public${filter}`);
  }

  getUserBalance(token:any) {

    const headedsrs = new HttpHeaders()
      .set('Authorization', `Bearer ${token}`)
      .set('content-type', 'application/json')
      .set('Access-Control-Allow-Origin', '*');

    let headers = new HttpHeaders();
    headers = headers.append('content-type', 'application/json');
    headers = headers.append('Access-Control-Allow-Origin', '*');
    headers = headers.append('Access-Control-Allow-Headers', 'Origin, X-Requested-With, Content-Type, Accept, Authorization');
    headers = headers.append('Authorization', `Bearer ${token}`);
    headers = headers.append('Access-Control-Allow-Methods', 'GET, POST, PUT, DELETE, PATCH, OPTIONS' )
    console.log(headers);

    return this.httpClient.get<userMe>(baseUrl + `user/me`, { headers });
  }



  changePassword(object: ChangePassword) {


    let headers = new HttpHeaders();
    headers = headers.append('content-type', 'application/json');
    headers = headers.append('Access-Control-Allow-Origin', '*');
    headers = headers.append('Access-Control-Allow-Headers', 'Origin, X-Requested-With, Content-Type, Accept, Authorization');
    headers = headers.append('Authorization', 'Bearer ' + sessionStorage.getItem('token'));
    headers = headers.append('Access-Control-Allow-Methods', 'GET, POST, PUT, DELETE, PATCH, OPTIONS' );
    console.log(headers);

    return this.httpClient.post(baseUrl + `user/change_password`, object, { headers });
  }

  getUser() {
    let headers = this.getHeader();
    console.log(headers);

    return this.httpClient.get<User>(baseUrl + `user/me`, { headers });
  }

  updateUser(userProfile: UserProfile) {

    let headers = this.getHeader();

    return this.httpClient.put(baseUrl + `user/update`, userProfile, { headers });
  }


  resendOtp(userProfile: UserProfile) {

    let headers = this.getHeader();

    let json = {
      mobile:  userProfile.mobile
    }
    return this.httpClient.post(baseUrl + `user/resend_otp`, json, { headers });
  }


  private getHeader() {
    let headers = new HttpHeaders();
    headers = headers.append('content-type', 'application/json');
    headers = headers.append('Access-Control-Allow-Origin', '*');
    headers = headers.append('Access-Control-Allow-Headers', 'Origin, X-Requested-With, Content-Type, Accept, Authorization');
    headers = headers.append('Authorization', 'Bearer ' + sessionStorage.getItem('token'));
    return headers.append('Access-Control-Allow-Methods', 'GET, POST, PUT, DELETE, PATCH, OPTIONS');
  }

}
