import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { faCartPlus } from '@fortawesome/free-solid-svg-icons';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { headers } from '../helper/header';
import {  FormBuilder,
  FormGroup,
  FormControl,
  Validators,} from '@angular/forms'
@Component({
  selector: 'app-cart-content',
  templateUrl: './cart-content.component.html',
  styleUrls: ['./cart-content.component.css'],
})
export class CartContentComponent implements OnInit {
  faCartPlus = faCartPlus;
  cartItem:any = [];
  existItem:any
  userVerified: any;

  confirmClicked = false;
  cancelClicked = false;
  popoverTitle = 'Items Delete';
  popoverMessage = 'You are about ot  detele this items';
  count:any
  text:any
  mymodel:any
  

  constructor(
    private formBuilder: FormBuilder,
    private dataservice: DataService,
    private router: Router,
    private http: HttpClient
  ) { }

  payNowForm = this.formBuilder.group({
    amountToRecharge: new FormControl('', [Validators.pattern("^[0-9]*$")])
  });

  ngOnInit(): void {

    this.dataservice.cart.subscribe((cart: any) => {
      this.cartItem = cart;
    });

    this.dataservice.count.subscribe((count: any) => {
      this.count = count;
    });

    // this.cartItem = JSON.parse(localStorage.getItem('item') ||'[]');  
    // this.count = JSON.parse(localStorage.getItem('count') ||'[]');
    
  }
  get amountToRecharge(){
    return this.payNowForm.get('amountToRecharge')
    }
  addtoCart(draw: any) {
    this.dataservice.addtoCart(draw);

  }
  removefromCart(draw: any) {
    this.dataservice.removefromCart(draw);
  }

  buyAll(draw: any) {
    this.dataservice.buyAll(draw);
  }
  deleteItem(draw: any) {
      this.dataservice.deleteItem(draw);
  }


 

  valuechange(UpdatedValue : any, item:any) :void{

 //const x = parseInt(UpdatedValue.data);
 console.log(UpdatedValue)
 console.log(item)
 this.dataservice.userEnter(UpdatedValue, item);
  
  }

}
