import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import {headers} from '../../helper/header'
import { HttpClient } from '@angular/common/http';


@Component({
  selector: 'app-forget-password',
  templateUrl: './forget-password.component.html',
  styleUrls: ['./forget-password.component.css']
})
export class ForgetPasswordComponent implements OnInit {

  success: boolean = false;
  _enterCode: boolean = false;
  _enterPhoneNumber: boolean = true;
  _sendVerificationCode: boolean = true;
  _verifiyCode: boolean = false;
  phoneMessegeError:any


  mobileForm = this.formBuilder.group({
    mobile: '',
  });
  otpForm = this.formBuilder.group({
    mobile: '',
    otp: '',
  });

  constructor(private formBuilder:FormBuilder,private http: HttpClient,) { }

  ngOnInit(): void { }

  sendVerificationCode() {
    const raw = JSON.stringify({
      mobile: this.mobileForm.value.mobile,
    });
    this.http
      .post('https://luck-buy.herokuapp.com/api/auth/forgot_password', raw,headers)
      .subscribe(
        (res) => {
          console.log(res)
          this._enterCode = true;
          this._enterPhoneNumber = false;
          this._sendVerificationCode = false;
          this._verifiyCode = true;
        },
        (error) => {
          console.log(error)
          this.phoneMessegeError = error.error.message
        }
      );

  
  }

  verifiyCode(){
    const raw = JSON.stringify({
      mobile: this.mobileForm.value.mobile,
      otp: this.otpForm.value.otp,
    });
    console.log(raw)
    this.http
      .post('https://luck-buy.herokuapp.com//api/auth/validate_forgot_password_otp', raw,headers)
      .subscribe(
        (res) => {
          console.log(res)
          this.success = true
        },
        (error) => {
          console.log(error)
          this.phoneMessegeError = error.error.message
        }
      );
  }
}
