import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class DataService {
  count: BehaviorSubject<number>;
  counter = 0;

  cart: BehaviorSubject<any>;
  cartItems: any = [];

  user: BehaviorSubject<any>;
  userName =  sessionStorage.getItem("userName");

  token: BehaviorSubject<any>;
  userToken:any  =  sessionStorage.getItem("token");

  verified: BehaviorSubject<any>;
  userVerified = sessionStorage.getItem("userVerified");

  draw: any;
  existItem: any;

  constructor() {
    this.user = new BehaviorSubject(this.userName);
    this.verified = new BehaviorSubject(this.userVerified);
    this.count = new BehaviorSubject(this.counter);
    this.cart = new BehaviorSubject(this.cartItems);
    this.token = new BehaviorSubject(this.userToken);
  }

 

  logout(){
    this.user = new BehaviorSubject(this.userName);
  }
  
  addtoCart(draw: any) {
    this.existItem = this.cartItems.find(
      (x: any) => x.productId == draw.productId
    );
    if (this.existItem) {
      this.cartItems = this.cartItems.map((x: any) =>
        x.productId === draw.productId
          ? { ...this.existItem, qty: this.existItem.qty + 1 }
          : x
      );
    } else {
      this.cartItems = [...this.cartItems, { ...draw, qty: 1 }];
    }

    let counter = 0;
    this.cartItems.forEach(function (x: any) {
      counter += x.qty;
    });

    this.count.next(counter);
    this.cart.next(this.cartItems);
    sessionStorage.setItem('item',JSON.stringify(this.cartItems)); 
    sessionStorage.setItem('count',JSON.stringify(counter));
  }

  userEnter(userInput:any, items:any) {
    this.existItem = this.cartItems.find(
      (x: any) => x.productId == items.productId
    );
    if (this.existItem) {
      this.cartItems = this.cartItems.map((x: any) =>
        x.productId === items.productId
          ? { ...this.existItem, qty: userInput }
          : x
      );
    } 

    let counter = 0;
    this.cartItems.forEach(function (x: any) {
      counter += x.qty;
    });

    this.count.next(counter);
    this.cart.next(this.cartItems);
    //localStorage.setItem('items',JSON.stringify(this.cartItems)); 
   // localStorage.setItem('count',JSON.stringify(counter));
  }


  removefromCart(draw: any) {
    this.existItem = this.cartItems.find(
      (x: any) => x.productId === draw.productId
    );
    if (this.existItem.qty === 1) {
      this.cartItems = this.cartItems.filter(
        (x: any) => x.productId !== draw.productId
      );
    } else {
      this.cartItems = this.cartItems.map((x: any) =>
        x.productId === draw.productId
          ? { ...this.existItem, qty: this.existItem.qty - 1 }
          : x
      );
    }

    let counter = 0;
    this.cartItems.forEach(function (itemQTY: any) {
      counter += itemQTY.qty;
    });

    this.count.next(counter);
    this.cart.next(this.cartItems);
   // localStorage.setItem('items',JSON.stringify(this.cartItems)); 
  }

  deleteItem(draw: any) {
    this.cartItems = this.cartItems.filter(
      (x: any) => x.productId !== draw.productId
    );

    localStorage.setItem('items',JSON.stringify(this.cartItems));
    
    let counter = 0;
    this.cartItems.forEach(function (itemQTY: any) {
      counter += itemQTY.qty;
    });

    this.count.next(counter);
    this.cart.next(this.cartItems);
   
  }

  buyAll(draw: any) {
    this.existItem = this.cartItems.find(
      (x: any) => x.productId == draw.productId
    );
    if (this.existItem) {
      this.cartItems = this.cartItems.map((x: any) =>
        x.productId === draw.productId
          ? {
              ...this.existItem,
              qty: this.existItem.totalDrawEntrants - draw.currentDrawEntrants,
            }
          : x
      );
      localStorage.setItem('items',JSON.stringify(this.cartItems)); 
    }
    let counter = 0;
    this.cartItems.forEach(function (itemQTY: any) {
      counter += itemQTY.qty;
    });

    this.count.next(counter);
    this.cart.next(this.cartItems);
    //localStorage.setItem('count',JSON.stringify(counter));
   // localStorage.setItem('items',JSON.stringify(this.cartItems)); 
  }
}
