import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../services/api.service'
import { faShoppingCart } from '@fortawesome/free-solid-svg-icons';
import {headers} from '../../helper/header'
import { HttpClient } from '@angular/common/http';
import {Draw} from '../../helper/draw.interface'
import { DataService } from '../../services/data.service'
import {public_headers} from '../../helper/public_headers'

@Component({
  selector: 'app-sold-out',
  templateUrl: './sold-out.component.html',
  styleUrls: ['./sold-out.component.css']
})
export class SoldOutComponent implements OnInit {
  faShoppingCart = faShoppingCart;
  filter: string = "SOLD_OUT";
  souldOut: any;
  _nextRound:any

  previews:any
  count:any
  constructor(private dataservice:DataService, private http:HttpClient, private ApiService:ApiService) { }

  ngOnInit(): void {
    this.ApiService.getDraw(this.filter).subscribe((drawData: any) => {
      this.souldOut = drawData.body
    })
    this.dataservice.count.subscribe(c => {
      this.count = c;
  });
  }
  
  nextRound(_id: any) {
    
    this.http
    .put<Draw>(`https://luck-buy.herokuapp.com/api/draw/nextRound/${_id}`,headers)
    .subscribe(
      (res) => {
        console.log(res);
        this._nextRound  = res.body
      },
      (error) => {
        console.log(error);
      }
    );
  
  }
  productPreview(_id:any){
    this.previews = this.souldOut.filter((draw:any) => draw.id == _id);
    //console.log(this.preview)
  }
  addtoCart(product:any) {

    this.dataservice.addtoCart(product);
}
}
