import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { User } from '../../helper/user.interface';
import { headers } from '../../helper/header';
import { Router } from '@angular/router';
import { faCheck } from '@fortawesome/free-solid-svg-icons';
import { baseUrl } from '../../helper/base_url';
import {
  SocialAuthService,
  GoogleLoginProvider,
  FacebookLoginProvider,
  SocialUser,
} from 'angularx-social-login';
import { HttpHeaders } from '@angular/common/http';
import { DataService } from '../../services/data.service';
@Component({
  selector: 'app-log-in',
  templateUrl: './log-in.component.html',
  styleUrls: ['./log-in.component.css'],
})
export class LogInComponent implements OnInit {
  socialUser: any = SocialUser;
  isLoggedin: boolean = false;
  userName = '';
  success: boolean = false;
  passwordError: boolean = false;
  errorMessage: string = '';
  faCheck = faCheck;
  logingUrl: string = '';
  socialMessageError: any;
  logSuccessMessage: any;
  ver: boolean = false;
  logInForm = this.formBuilder.group({
    mobile: '',
    password: '',
  });

  constructor(
    private datasevice: DataService,
    private router: Router,
    private formBuilder: FormBuilder,
    private http: HttpClient,
    private socialAuthService: SocialAuthService
  ) {}

  ngOnInit(): void {
    this.datasevice.user.subscribe((user: any) => {
      this.userName = user;
    });

    /* this.socialAuthService.authState.subscribe((user) => {
      this.socialUser = user;
      this.isLoggedin = user != null;
      console.log(this.socialUser);

      if (this.isLoggedin) {
        const headers = new HttpHeaders()
          .append('Content-Type', 'application/json')
          .append('Access-Control-Allow-Headers', 'Content-Type')
          .append('Access-Control-Allow-Origin', '*');

        const raw = JSON.stringify({
          token: this.socialUser.idToken,
          provider: this.socialUser.provider,
        });
        console.log(raw);
        this.logingUrl = 'auth/social';
        this.http
          .post(baseUrl + this.logingUrl, raw, { headers: headers })
          .subscribe(
            (res) => {
              console.log(res);
              this.success = true;
            },
            (error) => {
              console.log(error);
            }
          );
      }
    }); */
  }

  onSubmit(): void {
    // this.logInForm.getRawValue
    this.logingUrl = 'auth/signin';
    this.http
      .post<User>(
        baseUrl + this.logingUrl,
        this.logInForm.getRawValue(),
        headers
      )
      .subscribe(
        (res) => {
          /*store in session */
          sessionStorage.setItem('token', res.body.token);
          sessionStorage.setItem('userName', res.body.firstName);
          if (res.body.verified === true) {
            sessionStorage.setItem('userVerified', 'true');
          } else sessionStorage.setItem('userVerified', 'false');
   
          /*success message */
          this.success = true;
          this.logSuccessMessage = res.message;
          if (
            this.router.url === '/login_to_pay' ||
            this.router.url === '/phone_verification_to_pay'
          ) {
           setTimeout(function(){  location.reload(); }, 1000);
            window.location.href = '/#/checkout_list'
          } else{
            setTimeout(function(){  location.reload(); }, 2000);
           
            //window.location.href = '/#/'
          }
        },
        (error) => {
          this.errorMessage = error.error.message;
          /** console.log(err.status) **/
        },
        () => {
          console.log('HTTP request completed.');
          this.userName = this.userName;
        }
      );
  }

  loginWithGoogle(): void {
    this.socialAuthService.signIn(GoogleLoginProvider.PROVIDER_ID);
  }
  loginWithFacebook(): void {
    this.socialAuthService.signIn(FacebookLoginProvider.PROVIDER_ID);
  }

  logOut(): void {
    this.socialAuthService.signOut();
  }
}

