import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {faChalkboardTeacher, faSignOutAlt, faTasks, faUser, faUserLock} from '@fortawesome/free-solid-svg-icons';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {ApiService} from '../services/api.service';
import {DataService} from '../services/data.service';
import {ChangePassword} from '../model/ChangePassword';
import {UserProfile} from "../model/UserProfile";

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.css']
})
export class UserProfileComponent implements OnInit {

  faUser = faUser;
  faChalkboardTeacher = faChalkboardTeacher;
  faTasks = faTasks;
  faUserLock = faUserLock;
  faSignOutAlt = faSignOutAlt;

  personalDetails = true;
  userHistory = false;
  transactionHistory = false;
  changePassword = false;
  changePasswordSuccessNotificationAlert = false;
  userProfileSuccessNotificationAlert = false;
  mobileOTPvalidationPopup = false;
  invalidOtp = false;


  oldPassword = new FormControl('', [Validators.required, Validators.minLength(8), Validators.pattern('^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])(?=).{8,20}$')]);
  newPassword = new FormControl('', [Validators.required, Validators.minLength(8), Validators.pattern('^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])(?=).{8,20}$')]);
  confirmPassword = new FormControl('', [Validators.required, Validators.minLength(8), Validators.pattern('^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])(?=).{8,20}$')]);
  changePasswordObject = new ChangePassword('', '', '');
  formData: FormGroup = new FormGroup({
    oldPassword: this.oldPassword,
    newPassword: this.newPassword,
    confirmPassword: this.confirmPassword
  });


  userName = new FormControl();
  address = new FormControl();
  email = new FormControl('', [ Validators.email]);
  mobile = new FormControl('', [Validators.required, Validators.pattern('^(?:\\+971|00971|0)?(?:50|51|52|54|55|56|58|2|3|4|6|7|9)\\d{7}$')]);
  otp = new FormControl();


  userProfileFormData: FormGroup = new FormGroup({
    userName: this.userName,
    address: this.address,
    email: this.email,
    mobile: this.mobile,
    otp: this.otp
  });

  constructor(
    private apiService: ApiService,
    private datasevice: DataService,
    private router: Router
  ) {
    if (sessionStorage.getItem('userName') == null) {
      this.router.navigate(['/'])
    }
  }

  ngOnInit(): void {

    this.apiService.getUser().subscribe(value => {
      console.log(value.body);

      sessionStorage.setItem('mobile', value.body.mobile);
      this.userProfileFormData.setValue({
        userName: value.body.username,
       // address: value.body.address,
        email:  value.body.email,
        mobile:  value.body.mobile,
        otp: null
      });
    })
  }


  onSubmit() {
    if (this.formData.valid) {

      this.changePasswordObject.oldPassword = this.formData.value.oldPassword;
      this.changePasswordObject.newPassword = this.formData.value.newPassword;
      this.changePasswordObject.confirmPassword = this.formData.value.confirmPassword;

      this.apiService.changePassword(this.changePasswordObject).subscribe(value => {
        this.formData.reset();
        this.changePasswordSuccessNotificationAlert = true;
        setTimeout(() => {
          this.changePasswordSuccessNotificationAlert = false;
        }, 10000);
      });

    }
  }

  onUserProfileSubmit() {
    console.log(this.userProfileFormData)
    if (this.userProfileFormData.valid) {
      console.log(this.userProfileFormData)


      let userProfile = new UserProfile(this.userProfileFormData.value.userName,this.userProfileFormData.value.address,this.userProfileFormData.value.email,this.userProfileFormData.value.mobile,null);
      if(sessionStorage.getItem('mobile') !== this.userProfileFormData.value.mobile && !this.userProfileFormData.value.otp){
        this.apiService.resendOtp(userProfile).subscribe(value => {
        });
        this.mobileOTPvalidationPopup = true;
        return;

      }


      if(sessionStorage.getItem('mobile') !== this.userProfileFormData.value.mobile && this.userProfileFormData.value.otp){
        userProfile = new UserProfile(this.userProfileFormData.value.userName,this.userProfileFormData.value.address,this.userProfileFormData.value.email,this.userProfileFormData.value.mobile, this.userProfileFormData.value.otp);
      }
      console.log(userProfile);
      this.apiService.updateUser(userProfile)
        .subscribe(value => {
          this.mobileOTPvalidationPopup = false;
          this.userProfileSuccessNotificationAlert = true;
          setTimeout(() => {
            this.userProfileSuccessNotificationAlert = false;
          }, 10000);
        },error => {
          this.invalidOtp = true;
          setTimeout(() => {
            this.invalidOtp = false;
          }, 10000);
        })
    }
  }

  personalDetailsClick() {
    this.personalDetails = true;
    this.transactionHistory = false;
    this.changePassword = false;
    this.userHistory = false;

    this.mobileOTPvalidationPopup = false;
    this.apiService.getUser().subscribe(value => {
      console.log(value.body);
      sessionStorage.setItem('mobile', value.body.mobile);
      this.userProfileFormData.setValue({
        userName: value.body.username,
       // address: value.body.address,
        email:  value.body.email,
        mobile:  value.body.mobile,
        otp: null
      });
    })
  }

  _userHistory() {
    this.personalDetails = false;
    this.transactionHistory = false;
    this.changePassword = false;
    this.userHistory = true;
  }

  _transactionHistory() {
    this.personalDetails = false;
    this.transactionHistory = true;
    this.changePassword = false;
    this.userHistory = false;
  }

  _changePassword() {
    this.personalDetails = false;
    this.transactionHistory = false;
    this.changePassword = true;
    this.userHistory = false;
  }


  logout() {
    sessionStorage.clear();
    this.datasevice.logout();
    window.location.href = '/';
  }

  _wallet() {
    this.router.navigate(['/payment']);
  }
}
