import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import {headers } from '../../helper/header';
import {User} from '../../helper/user.interface'

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css'],
})

export class RegisterComponent implements OnInit {

  _enterPassword: boolean = false;
  _enterYourPhoneNumber: boolean = true;
  _enterOTP: boolean = false;

  success: boolean = false;
  signUpSuccessMessage:any
  otpMessegeError: any;
  phoneMessegeError: any
  passwordMessegeError: any;

  phoneForm = this.formBuilder.group({
    mobile: '',
    deviceId: 'aesae',
    os: window.navigator.platform,
    deviceName: 'aesae',
    role: ['ROLE_USER'],
  });

  otpForm = this.formBuilder.group({
    mobile: '',
    otp: '',
  });

  signUpForm = this.formBuilder.group({
    mobile: '',
    password: '',
    confirmPassword: '',
    otp: ''
  });

  constructor(private formBuilder: FormBuilder, private http: HttpClient) {}
  ngOnInit(): void {}


 
  registerPhone() {
    const raw = JSON.stringify({
      mobile: this.phoneForm.value.mobile,
      deviceId: this.phoneForm.value.deviceId,
      os: this.phoneForm.value.os,
      deviceName: this.phoneForm.value.deviceName,
      role: ['ROLE_USER'],
    });

    this.http
      .post('https://luck-buy.herokuapp.com/api/auth/signup', raw, headers)
      .subscribe(
        (res) => {
          console.log(res);
          this._enterPassword = false;
          this._enterYourPhoneNumber = false;
          this._enterOTP = true;;
        },
        (error) => {
          console.log(error)
          this.phoneMessegeError = error.error.message
        }
      );
  }

  
  verifiyOTP() {
    const raw = JSON.stringify({
      mobile: this.phoneForm.value.mobile,
      otp: this.otpForm.value.otp,
    });

    this.http
      .post('https://luck-buy.herokuapp.com/api/auth/validate_signup_otp', raw, headers)
      .subscribe(
        (res) => {
          console.log(res);
          this._enterPassword = true;
          this._enterYourPhoneNumber = false;
          this._enterOTP = false;
        },
        (error) => {
          console.log(error)
          this.otpMessegeError = error.error.message
        }
      );
  }

  signUp() {
    const raw = JSON.stringify({
      mobile: this.phoneForm.value.mobile,
      password: this.signUpForm.value.password,
      confirmPassword: this.signUpForm.value.confirmPassword,
      otp: this.otpForm.value.otp,
    });

    this.http
      .post<User>('https://luck-buy.herokuapp.com/api/auth/validate_signup_password', raw,headers)
      .subscribe(
        (res) => {
          console.log(res);
          sessionStorage.setItem('token', res.body.token)
          sessionStorage.setItem('userName',res.body.username)
          this.signUpSuccessMessage  = res.message
          this.success = true;
        },
        (error) => {
          console.log(error)
          this.passwordMessegeError = error.error.message
        }
      );
  }

  onCloselogInModal() {
    //window.location.href = '';
  }
}
