import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';

@Component({
  selector: 'app-check-phone-verify',
  templateUrl: './check-phone-verify.component.html',
  styleUrls: ['./check-phone-verify.component.css']
})
export class CheckPhoneVerifyComponent implements OnInit {
  userVerified:any
  constructor(private  dataservice:DataService) { }

  ngOnInit(): void {
    this.dataservice.verified.subscribe((verified: string) => {
      this.userVerified = verified;
      console.log(this.userVerified)
    });
  }

}
