import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';


@Component({
  selector: 'top-up-app-payment-messege',
  templateUrl: './top-up-payment-messege.component.html',
  styleUrls: ['./top-up-payment-messege.component.css']
})


export class TopUpPaymentMessegeComponent implements OnInit {

  successful:boolean = false
  unsuccessful:boolean = false
  partialsuccess:boolean = false
  fail:boolean  =false
  messege_id:any
  topUp:any
  

  constructor(private _Activatedroute: ActivatedRoute,) { }

  ngOnInit(): void {
    this.messege_id = this._Activatedroute.snapshot.paramMap.get('messege_id');
    
    if(this.messege_id == 1){
      this.successful = true
    } else if(this.messege_id == 2){
      this.partialsuccess = true
    } else if(this.messege_id == 3){
      this.topUp = true
    } else if(this.messege_id == 4){
      this.fail = true
    }
  }

}
