import { Component, OnInit } from '@angular/core';
import { faShoppingCart } from '@fortawesome/free-solid-svg-icons';
import { ApiService } from '../../services/api.service';
import { DataService } from '../../services/data.service';
import {public_headers} from '../../helper/public_headers'

@Component({
  selector: 'app-best-seller',
  templateUrl: './best-seller.component.html',
  styleUrls: ['./best-seller.component.css'],
})
export class BestSellerComponent implements OnInit {
  faShoppingCart = faShoppingCart;
  draws: any;
  filter: string = 'BEST_SELLER';
  drawData: any;
  count: any;
  previews: any;
  renderer:any
  cartItems:[] = []

  constructor(
    private ApiService: ApiService,
    private dataservice: DataService
  ) {}

  ngOnInit(): void {

    this.ApiService.getDraw(this.filter).subscribe((drawData: any) => {
      this.draws = drawData.body;
    })
    


    this.dataservice.count.subscribe((countter:any) => {
      this.count = countter;
    });
  }
  productPreview(_id: any) {
    this.previews = this.draws.filter((draw: any) => draw.id == _id);
    //console.log(this.preview)
  }

  addtoCart(draw: any) {
    this.dataservice.addtoCart(draw);
    this.dataservice.cartItems.subscribe((items:any) => {
      this.draws = items;
      console.log(this.draws)
    });
  }
  removefromCart(draw: any) {
    this.dataservice.addtoCart(draw);
  }


}
