import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { Router } from '@angular/router'
@Component({
  selector: 'app-display-checkout-list',
  templateUrl: './display-checkout-list.component.html',
  styleUrls: ['./display-checkout-list.component.css']
})
export class DisplayCheckoutListComponent implements OnInit {
  cartItem: any;
  userVerified: any
  constructor(private dataservice: DataService,
    private router: Router,) { }

  ngOnInit(): void {
    this.dataservice.cart.subscribe((cart: any) => {
      this.cartItem = cart;
      console.log(this.cartItem);
    });

    this.dataservice.verified.subscribe((verified: string) => {
      this.userVerified = verified;
      console.log(this.userVerified);
    });
  }
}
