import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from '../services/api.service';
import { Meta, Title } from '@angular/platform-browser';
import { DeviceDetectorService } from 'ngx-device-detector';
import {public_headers} from '../helper/public_headers'
@Component({
  selector: 'app-draw-details',
  templateUrl: './draw-details.component.html',
  styleUrls: ['./draw-details.component.css'],
})
export class DrawDetailsComponent implements OnInit {
  draws: any;
  draw_id: any;
  filter: string = 'BEST_SELLER';
  previews: any;
  deviceInfo:any
  constructor(private deviceService: DeviceDetectorService,
    private title: Title,
    private meta: Meta,
    private metaService: Meta,
    private _Activatedroute: ActivatedRoute,
    private ApiService: ApiService
  ) {   this.epicFunction();
  
  
  
  
 
  
  }

  ngOnInit(): void {
    this.draw_id = this._Activatedroute.snapshot.paramMap.get('draw_id');
    this.ApiService.getDraw(this.filter).subscribe((drawData: any) => {
      this.draws = drawData.body;
      //  console.log(this.draws)
      this.previews = this.draws.filter((draw: any) => draw.id == 5);
      // console.log(this.previews[0].productDTO.productName)
      // this.title.setTitle(this.previews[0].productDTO.productName);
      // this.meta.updateTag({ property  : 'og:image', content: this.previews[0].productDTO.thumbnailUrl });
      // this.meta.updateTag({ property: 'og:title', content: this.previews[0].productDTO.productName });
      // this.meta.updateTag({ property: 'og:description', content: this.previews[0].productDTO.productDescription});

      this.meta.addTags([
        {
          property: 'og:image',
          itemprop: 'image',
          content:
            'https://gallant-goldwasser-67c193.netlify.app/assets/img/lap.jpg',
        },
        {
          property: 'og:title',
          content: this.previews[0].productDTO.productName,
        },
        {
          property: 'og:description',
          content: this.previews[0].productDTO.productDescription,
        },
        {
          property: 'og:url',
          content: 'https://naughty-morse-  11221a.netlify.app',
        },
      ]);
    });
  }

  epicFunction() {
    console.log('hello `Home` component');
    this.deviceInfo = this.deviceService.getDeviceInfo();
    const isMobile = this.deviceService.isMobile();
    const isTablet = this.deviceService.isTablet();
    const isDesktopDevice = this.deviceService.isDesktop();
    console.log(this.deviceInfo);
    console.log(isMobile);  // returns if the device is a mobile device (android / iPhone / windows-phone etc)
    console.log(isTablet);  // returns if the device us a tablet (iPad etc)
    console.log(isDesktopDevice); // returns if the app is running on a Desktop browser.
  }

  

}
