import { Component, OnInit } from '@angular/core';
import { faShoppingCart } from '@fortawesome/free-solid-svg-icons';
import { ApiService } from '../../services/api.service';
import { DataService } from '../../services/data.service';
import {public_headers} from '../../helper/public_headers'


@Component({
  selector: 'app-new-arrival',
  templateUrl: './new-arrival.component.html',
  styleUrls: ['./new-arrival.component.css'],
})
export class NewArrivalComponent implements OnInit {
  faShoppingCart = faShoppingCart;
  filter: string = 'NEW_ARRIVAL';
  newArrival: any;
  previews: any;
  count:any
  constructor(
    private dataservice: DataService,
    private ApiService: ApiService
  ) {}

  ngOnInit(): void {
    this.ApiService.getDraw(this.filter).subscribe((drawData: any) => {
      this.newArrival = drawData.body;
    });
    this.dataservice.count.subscribe(c => {
      this.count = c;
  });
  }
  productPreview(_id: any) {
    this.previews = this.newArrival.filter((draw: any) => draw.id == _id);
    console.log(this.previews);
  }
  addtoCart(product:any) {

    this.dataservice.addtoCart(product);
}
}
